/* *************** */
/* *** WRAPPER *** */
/* *************** */
.app__aboutus {
  position: relative;
}

/* *************** */
/* *** OVERLAY *** */
/* *************** */
.app__aboutus-overlay {
  position: absolute;
  inset: 0;
}

.app__aboutus-overlay img {
  width: 391px;
  height: 415px;
  z-index: 0;
}

/* ************************* */
/* *** CONTENT CONTAINER *** */
/* ************************* */
.app__aboutus-content {
  width: 100%;
  z-index: 2;
}


/* **************** */
/* *** About Us *** */
/* **************** */
.app__aboutus-content_about {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  text-align: right;
}

/* ***************** */
/* *** Knife Img *** */
/* ***************** */
.app__aboutus-content_knife {
  margin: 2rem 4rem;
}

.app__aboutus-content_knife img {
  height: 910px;
}

/* ******************* */
/* *** Our History *** */
/* ******************* */
.app__aboutus-content_history {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  text-align: left;
}

/* ****************** */
/* *** TYPOGRAPHY *** */
/* ****************** */
.app__aboutus-content_about p,
.app__aboutus-content_history p {
  margin: 2rem 0;
  color: var(--color-grey);
}

/* ******************** */
/* *** LARGE STYLES *** */
/* ******************** */
@media screen and (max-width: 2000px) {
  .app__aboutus-content_knife img {
    height: 1110px;
  }

  .app__aboutus-content_about p,
  .app__aboutus-content_history p {
    margin: 4rem 0;
  }
}

/* ********************* */
/* *** MEDIUM STYLES *** */
/* ********************* */
@media screen and (max-width: 1150px) {
  .app__aboutus-content {
    flex-direction: column;
  }

  .app__aboutus-content_knife {
    margin: 4rem 0;
  }
}

/* ******************** */
/* *** SMALL STYLES *** */
/* ******************** */
@media screen and (max-width: 650px) {
  .app__aboutus-overlay img {
    width: 80%;
    height: 320px;
  }
}